    z-index: 999;
    left: 50%;
    top: 50%;
    -webkit-transform: translate(-50%, -50%);
    transform: translate(-50%, -50%);
    box-sizing: border-box;
    width: 16.875rem;
    border-radius: .3125rem;
    background: rgba(255, 255, 255, .95);
    text-align: center;
    -webkit-backdrop-filter: blur(.625rem)
}

.m-alert header {
    padding: 1.25rem;
    color: #000;
    line-height: 1.25
}

.m-alert header h2 {
    font-size: 1rem
}

.m-alert header h3 {
    font-size: .75rem;
    margin: .5rem 0 0 0
}

.m-alert footer {
    font-size: 1rem;
    border-top: 1px solid #b6b6b6
}

.iosx2 .m-alert footer {
    border-top: .5px solid #b6b6b6
}

.iosx3 .m-alert footer {
    border-top: .36px solid #b6b6b6
}

.m-alert footer .m-box-col+.m-box-col {
    border-left: 1px solid #b6b6b6
}

.iosx2 .m-alert footer .m-box-col+.m-box-col {
    border-left: .5px solid #b6b6b6
}

.iosx3 .m-alert footer .m-box-col+.m-box-col {
    border-left: .36px solid #b6b6b6
}

.m-alert footer a {
    display: block;
    line-height: 2.8125rem;
    color: #007aff
}

@supports (overflow: -webkit-marquee) and (justify-content:inherit) {
    .m-alert {
        background: rgba(255, 255, 255, .8)
    }
}

.m-dialog {
    position: fixed;
    z-index: 999;
    left: 50%;
    top: 50%;
    -webkit-transform: translate(-50%, -50%);
    transform: translate(-50%, -50%);
    -webkit-backface-visibility: hidden;
    backface-visibility: hidden;
    box-sizing: border-box;
    width: 17.5rem;
    background: rgba(255, 255, 255, .95);
    -webkit-backdrop-filter: blur(.625rem);
    border-radius: .75rem;
    text-align: center;
    overflow: hidden
}

.m-dialog header {
    padding: 20px 12px
}

.m-dialog h2 {
    font-size: 1.0625rem
}

.m-dialog h2+h3 {
    margin-top: 16px
}

.m-dialog h3 {
    font-size: .875rem;
    line-height: 1.5;
    color: #333;
    font-weight: 300
}

.m-dialog .m-dialog-title {
    margin-bottom: 1.375rem
}

.m-dialog .m-dialog-form input {
    box-sizing: border-box;
    padding: 0 .5625rem;
    border: 1px solid #c8c8c8;
    box-shadow: inset 0 2px 3px 0 rgba(0, 0, 0, .05);
    border-radius: .3125rem;
    height: 2.25rem;
    width: 100%
}

.iosx2 .m-dialog .m-dialog-form input {
    border: .5px solid #c8c8c8
}

.iosx3 .m-dialog .m-dialog-form input {
    border: .36px solid #c8c8c8
}

.m-dialog .m-btn {
    border: 0;
    border-radius: 0;
    background-color: transparent;
    height: 2.8125rem;
    line-height: 2.8125rem
}

.m-dialog .m-btn.m-btn-text-orange:active {
    color: #FF8200
}

.m-dialog .m-btn:active {
    background-color: #E6E6E6
}

.m-dialog .m-btm-btns {
    margin: 0 -1px
}

.m-dialog .m-box-col {
    border-top: 1px solid #d0d0d0;
    border-left: 1px solid #d0d0d0;
    margin: 0
}

.iosx2 .m-dialog .m-box-col {
    border-top: .5px solid #d0d0d0
}

.iosx3 .m-dialog .m-box-col {
    border-top: .36px solid #d0d0d0
}

.iosx2 .m-dialog .m-box-col {
    border-left: .5px solid #d0d0d0
}

.iosx3 .m-dialog .m-box-col {
    border-left: .36px solid #d0d0d0
}

@supports (overflow: -webkit-marquee) and (justify-content:inherit) {
    .m-dialog {
        background: rgba(255, 255, 255, .8)
    }
}

.m-actionsheet {
    position: fixed;
    z-index: 999;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(255, 255, 255, .4);
    -webkit-backdrop-filter: blur(.625rem)
}

.m-actionsheet .m-actionsheet-list .m-actionsheet-item+.m-actionsheet-item {
    border-top: 1px solid transparent
}

.iosx2 .m-actionsheet .m-actionsheet-list .m-actionsheet-item+.m-actionsheet-item {
    border-top: .5px solid transparent
}

.iosx3 .m-actionsheet .m-actionsheet-list .m-actionsheet-item+.m-actionsheet-item {
    border-top: .36px solid transparent
}

.m-actionsheet .m-actionsheet-list+.m-actionsheet-item {
    margin-top: 5px
}

.m-actionsheet .m-actionsheet-item {
    display: block;
    background-color: rgba(255, 255, 255, .8);
    background-clip: padding-box;
    height: 3.25rem;
    line-height: 3.25rem;
    text-align: center;
    font-size: 16px;
    color: #333
}

.m-actionsheet .m-actionsheet-item:active {
    background-color: #E6E6E6
}

.m-actionsheet .m-actionsheet-warn {
    color: #F83232
}

@supports (overflow: -webkit-marquee) and (justify-content:inherit) {
    .m-actionsheet {
        background: rgba(255, 255, 255, .8)
    }
}

